import * as React from 'react';


import {useEffect} from "react";
import {inlineJs} from "../../../../helpers/jsloader";
import Js1 from  '!!raw-loader!./vendor/js1.js';
import Js2 from  '!!raw-loader!./vendor/js2.js';
import Js3 from  '!!raw-loader!./vendor/js3.js';
import Js4 from  '!!raw-loader!./vendor/js4.js';
import Js5 from  '!!raw-loader!./vendor/js5.js';

import {BackBtn} from "../../../../components/backBtn";
import {ROUTES} from "../../../../config";
export const NoiseBlob = () => {
  useEffect(()=>{
    setTimeout(()=>{
      inlineJs([ Js1,Js2,Js3,Js4,Js5]);
    }, 1);

  },[])
  return (
    <div >
      <BackBtn link={ROUTES.gallery} external/>

    </div>
  );
};
